body {
  margin: 0px;
  padding: 0px;
  background-color: #dacdbd;
}

.header {
  background-image: url('images/wideGaTech.jpg');
  width: 100%;
  height: 420px;
  position: absolute;
  top: 0px;
  z-index: 1;
}

.page {
  position: absolute;
  background-color: rgba(255, 255, 255, 0.95);
  width: 60%;
  margin-left: 20%;
  margin-top: 10%;
  padding: 2%;
  z-index: 3;
}

h1, p, ul, li {
  padding: 0px;
  margin: 0px;
}

ul {
  margin-left: 4%;
  margin-top: 2%;
  font-size: large;
}

p {
  margin-top: 2%;
  font-size: large;
}

b {
  color: #7c2613;
}

.buttonSet {
  display: flex;
  justify-content: space-around;
  margin-left: 10%;
  margin-right: 10%;
}

button {
  display: flex;
  padding: 1%;
  border: none;
  border-radius: 10px;
  background-color: #dacdbd;
  font-weight: bold;
  min-height: 40px;
  width: 100%;
}

button:hover {
  cursor: pointer;
}

span {
  margin: auto;
}

.bar {
  width: 80%;
  margin-left: 10%;
  margin-top: 2%;
  border: 1px #7c2613 solid;
}

a {
  color: #7c2613;
}

.link {
  width: 15%;
}

img {
  width: 100%;
  border: 2px solid black;
}

.left {
  width: 65%;
  display: inline-block;
}

.right {
  max-height: 50px;
  width: 30%;
  display: inline-block;
  margin-left: 5%;
}